/*  Footer  */
.footer {
    margin-top: 100px;
    background-color: $purple-light;
    color: $white;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: top;
        flex-wrap: wrap;
    }
    &__column {
        margin: 0 30px 0 0;
    }
    &__contacts {
        display: flex;
        align-items: center;
        img {
            margin-right: 10px;
        }
    }
    &__logo {
        margin: 20px 0;
    }
    &__text {
        max-width: 300px;
        margin: 0 auto;
        line-height: 1.5;
        margin-bottom: 30px;
    }
    &__social {
        img {
            margin-right: 30px; 
        }
    }
    &__menu {
        padding: 0;
        &-link {
            color: $white;
            line-height: 2;
            &:hover {
                color: $yellow;
            } 
        }
    }
    &__form-input {
        margin-bottom: 15px;
        max-width: 80%;
    }
}