/*  Slider  */
.team-slider {
    padding: 30px 0 120px;
    &__text {
        margin-bottom: 50px; 
    }
}
.slider {
    margin: 0 -20px;
    &__item {
        padding: 0 20px; 
        &-img {
            width: 350px;
            height: 400px;
            border-radius: 10px;
            object-fit: cover;
            margin-bottom: 25px;
        }
        &-info {
            margin-bottom: 10px;
            line-height: 28px;
        }
        &-author {
            text-decoration: none;
            font-weight: 700;
            color: #544837;
        }
        &-title  {
            text-decoration: none;
            font-size: 24px;
            line-height: 34px;
            font-weight: 700;
            transition: all .3s;
            margin-bottom: 80px;
            display: inline-block;
            &:hover {
                color: #ff9900;
            }
        }
    }
}
.slick {
    &-slide {
        outline: #fff;
    }
    &-dots {
        list-style: none;
        display: flex;
        justify-content: center;
        button {
            width: 16px;
            height: 16px;
            border-radius: 50%;
            border: none;
            font-size: 0;
            background-color: #EFEFEF;
            padding: 0;
            margin: 0 5px; 
        }
    }
    &-active{
        button {
            background-color: #F79B15;
        }
    }
}
.testimonial {
    background-color: $purple-light;
    border-radius: 10px;
    margin-right: 20px;
    padding: 30px;
    margin: 20px;
    &-top {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
    }
    &-bottom {
        line-height: 1.8;
    }
    img {
        margin-right: 20px;
        width: 80px;
        height: 80px;
        border-radius: 50%;
    }
}