/*  Header  */
.header {
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 20px; 
    }
}
.menu {
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 18px; 
    }
    &__list-item:hover ul{
        display: block;
    }
    &__list-item + &__list-item {
        margin-left: 30px;
    }
    &__list-link {
        color: $white;
        &:hover {
            color:  $yellow; 
        }
    }
    &-btn {
        display: none;
    }
}