/*  Clients  */
.clients {
    &__inner {
        display: flex;
        justify-content: center;
        align-items: center; 
        flex-wrap: wrap;
    }
    &__card {
        border: 1px solid #ccc;
        border-radius: 10px;
        padding: 50px;
        margin: 20px;
        text-align: center; 
        &:hover {
            box-shadow: 0 0 10px #180d41;
            border: none;
        }
        img {
            width: 80px;
            height: 80px; 
        }
    }
}