/* Work  */
.work {
    &__images {
        width: 50vw;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;
        &--1,
        &--2 {
            width: 24vw;
            height: 25vh;
            border-radius: 10px;
        }
    }
    &__image {
        width: 50vw;
        height: 50vh;
        margin: 0 auto;
        margin-bottom: 30px;
    }
    &-img {
        width: 100%;
        height: 100%;
        border-radius: 10px;
    }
    &__content {
        width: 50vw;
        margin: 0 auto;
        text-align: justify;
        margin-bottom: 50px;
        &-title,
        &-text {
            margin-bottom: 30px;
        }
        &-text {
            line-height: 1.8;
        }
    }
}