/*  Settings  */
body {
    background-color: $background-color;
    color: $white;
    font-family: $main-font;
}
.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}
.wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(#f00, #f0f);
    clip-path: circle(25% at right 60%);
}
.wrapper::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(#2196f3, #e91e63);
    clip-path: circle(20% at 10% 10%);
    z-index: -10;
}
.section {
    padding: 50px 0;
    text-align: center;
}
a {
    text-decoration: none;
    color: #000;
}
ul {
    list-style: none;
}
.title {
    text-align: center;
    font-size: 48px;
    font-weight: 700;
    color: $white;
    font-family: $main-font;
}
.active {
    text-align: center;
    text-transform: uppercase;
    color: $green;
    margin-bottom: -20px;
}
.button {
    display: inline-block;
    border-radius: 10px;
    background: linear-gradient($orange, $yellow);
    border: none;
    padding: 20px 40px;
    color: $white;
    font-weight: 700;
    font-size: 18px;
}
.button:hover {
    background: $yellow;
}
.form-input {
    padding: 20px;
    border-radius: 10px;
    border: 1px solid #ccc;
    max-width: 300px;
}
#typed {
    display: inline;
}
.back-to-top {
    background: $green;
    border-radius: 50%;
    position: fixed;
    bottom: -50px;
    padding: 10px 12px;
    right: 10px;
    cursor: pointer;
    transition: 0.3s all;
    z-index: 10;
}
.back-to-top.active {
    bottom: 50px;
    transition: 0.3s all;
}
.top-btn {
    width: 24px;
    height: 24px;
}
.error__box{
    min-height: 80vh;
    text-align: center;
    border-radius: 30px;
    padding: 30px;
    backdrop-filter: blur(5px);
    background: rgba(255, 255, 255, 0.1);
    h1 {
        font-size: 80px;
    }
    h2 {
        font-size: 70px;
        margin-bottom: 50px;
    }
}



