/*  Media  */
@media (max-width: 1135px) {
    .first-page {
        &__title{
            font-size: 60px;
            span {
                font-size: 60px;
            }
        }
    }
}
@media (max-width: 995px) {
    .first-page {
        &__title{
            font-size: 50px;
            span {
                font-size: 50px;
            }
        }
    }
    .portfolio {
        &__inner {
            flex-wrap: wrap;
            width: 50vw;
            margin: 0 auto; 
        }
    }
    .pricing {
        &__inner {
            flex-wrap: wrap;
        }
        &__item {
           width: 60vw;
           margin: 0 auto;
           margin-bottom: 50px;
        }
    }
    .contact {
        padding: 50px;
        &-container {
            flex-direction: column;
        }
        &__info {
            margin-bottom: 40px;
            width: 60vw;
        }
        &__form {
            width: 60vw;
        }
        &__inputs {
            display: block; 
        }
    }
    .services {
        &__inner {
            flex-wrap: wrap;
        }
        &__card {
            width: 60vw;
            margin: 0 auto;
            margin-bottom: 50px;
        }
    }
}
@media (max-width: 860px) {
    .first-page {
        &__title{
            font-size: 47px;
            span {
                font-size: 47px;
            }
        }
    }
    .work {
        &__image,
        &__images,
        &__content {
            width: 80vw;
        }
        &__images {
            &--1,
            &--2 {
                width: 38vw;
            }
        }
    }
}
@media (max-width: 769px) {
    .menu {
        &__list {
            display: block;
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            background-color: $purple-light;
            text-align: center;
            transition: all .3s;
            transform: translateY(-300%);
            min-height: 100vh;
            z-index: 20;
            margin: 0;
            padding: 0;
            padding-top: 70px;
            &--active {
                transform: translateY(0%);
                transition: all .3s;
            }
            &-link {
                display: block;
                padding: 30px 0;
                color: #fff;
            }
        }
        &-btn {
            display: block;
            width: 50px;
            height: 50px;
            position: relative;
            z-index: 10;
            span,
            span::before,
            span::after {
                position: absolute;
                width: 40px;
                height: 2px;
                top: 50%;
                margin-top: -1px;
                left: 50%;
                margin-left: -20px;
                background-color: $white;
            }
            span::before,
            span::after {
                content:'';
                display: block;
                transition: 0.2s;
            }
            span::before {
                transform: translateY(-10px);
            }
            span::after {
                transform: translateY(10px);
            }
            &__active {
                z-index: 21;
                span {
                    height: 0; 
                    &::before {
                        transform: rotate(45deg); 
                    }
                    &::after {
                        transform: rotate(-45deg);
                    }
                }
            }
        }
    }
    .menu__list-item + .menu__list-item{
        margin-left: 0;
    }
    .first-page {
        &__inner {
            // display: block;
            
            flex-direction: column-reverse;
            text-align: center;
        }
        &__content,
        &__img {
            width: 100%;
            margin-left: 0;
        }
        &__title {
            font-size: 72px;
            span {
                font-size: 72px;
            }
        }
        &__content {
            &-center {
                h1 {
                    font-size: 72px;
                    span {
                        font-size: 72px; 
                    }
                }
            }
        }
    }
    .about {
        &__image {
            width: 100%;
        }
        &__content  {
            width: 100%;
            margin-left: 0;
        }
        &__inner{
            flex-direction: column-reverse;
        }
    }
    .team {
        &__inner {
            padding: 100px 0;
        }
        &__item {
            display: none;  
        }
    }
    .price {
        &__inner {
            flex-wrap: wrap;
            width: 60vw;
            margin: 0 auto;
        }
    }
    .contact {
        &__info,
        &__form {
            width: 80vw;
        }
    }    
}
@media (max-width: 650px) {
    .portfolio {
        &__inner {
            width: 90vw;
        }
    }
    .price {
        &__inner {
            width: 90vw;
        }
    }
    .work {
        &__inner {
            padding: 10px; 
        }
        &__images {
            display: block; 
            &--1,
            &--2 {
                width: 80vw;
                height: 50vh;
                margin-bottom: 30px;
            }
        }
    }
    .newsletter {
        &__image {
            display: none;
        }
    }
}
@media (max-width: 478px) {
    .circle {
        display: none;
    }
}
@media (max-width: 375px) {
    .first-page {
        padding: 0;
        &__inner {
            padding: 10px;
        }
        &__title {
            font-size: 30px; 
            span {
                font-size: 30px;  
            }
        }
        &__content {
            &-center {
                h1 {
                    font-size: 60px;
                    span {
                        font-size: 60px; 
                    }
                }
            }
        }
    }
    .title {
        font-size: 30px;
    }
    .circle {
        &-section,
        &-content  {
            padding: 0;
        }
    }
    .services {
        &__inner {
            width: 90vw; 
        } 
    }
    .price {
        &::before,
        &::after {
            display: none; 
        }
    }
    .newsletter {
        &__inner {
            padding: 50px 10px;
        }
        &__left {
            width: 280px;
        }
        &__form-input {
            width: 280px;
        }
    }
    .contact {
        padding: 10px;
        &__img {
            display: none;
        }
        &-container {
            width: 80%;
        }
        &__form {
            width: 90%;
            text-align: center;
        }
        &__input {
            width: 90%;
        }
    }
    .footer {
        // text-align: center;
        // margin-top: 25px;
        &__column {
            margin-right: 0;
            width: 100%;
        }
        &__social {
            display: flex;
            justify-content: space-between;
            img {
                margin-right: 0;
            }
        }
    }
}