/*  Portfolio  */
.portfolio {
    &__inner {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    &__card {
        background-color: $purple-light;
        text-align: center;
        border-radius: 10px;
        overflow: hidden;
        margin: 10px;
        margin-bottom: 50px;
        padding-bottom: 30px;
        &-content {
            padding: 20px;
            color: $white;
        }
        &-image,
        &-title {
            margin-bottom: 20px;
        }
        &-img {
            width: 100%;
        }
        &-text {
            line-height: 1.8;
        }
    }
}
.portfolio-page {
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    text-align: center;
    margin-bottom: 50px;
    &__card {
        width: 300px;
        height: 300px;
        border-radius: 10px;
        padding: 20px;
        position: relative;
        perspective: 1000px;
        text-align: center;
        line-height: 1.8;
        margin: 20px;
    }
}
.front, .back{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
    backface-visibility: hidden;
    border-radius: 10px;
}
.front {
    background-repeat: no-repeat;
    background-size: cover;
}  
.back {
    background: linear-gradient($orange, $yellow);
    color: $white;
    transform: rotateY(180deg);
}
.portfolio-page__card:hover .front {
    transform: rotateY(180deg);
}
.portfolio-page__card:hover .back {
    transform: rotateY(360deg);
}
.portfolio-card--01 .front  {
    background-image: url(../images/portfolio/portfolio-1.jpg);
}
