/*  Newsletter  */
.newsletter {
    padding-top: 100px;
    position: relative;
    &::after {
        content: "";
        background-image: url('../../../build/assets/images/3d/shape-2.svg');
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        animation: image 5s linear infinite;
        z-index: -10; 
    }
    &__inner {
        background: rgba($purple-light, 0.2);
        backdrop-filter: blur(5px);
        border-radius: 30px;
        padding: 50px;
        color: $white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }
    &__left-subtitle,
    &__left-title {
        text-align: left;
        margin-bottom: 10px;
    }
    &__left-text {
        margin-bottom: 30px;
    }
    &__form-input {
        margin: 0 30px 30px 0;
        max-width: 300px;
    }
    &__image {
        animation: image 3s linear infinite;
    }
}