.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    &-item {
        margin: 20px;
        font-size: 24px;
        border: 1px solid $yellow;
        color: $yellow;
        border-radius: 10px;
        padding: 5px 15px;
    }
    &-item:hover {
        background-color: $yellow;
        color: $white;
        border: none;
    }
}