/* Team */
.team {
    &-section {
        &__inner {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            margin-bottom: 50px;
        }
        &__card {
            background: $purple-light; 
            overflow: hidden;
            text-align: center;
            border-radius: 10px;
            margin: 10px;
            &-image {
                width: 250px;
                height: 300px;
            }
        }
    }
}