/*  Contact  */
.contact {
    position: relative;
    padding: 50px 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    &-container {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 30px;
    }
    &__image {
        width: 50%;
        display: flex;
        flex-direction: column;
    }
    &__input{
        position: relative;
        width: 100%;
        margin-top: 10px;
        input,
        textarea {
            width: 80%;
            padding: 15px;
            margin: 10px 0;
            background: rgba(255, 255, 255, 0.1);
            border-radius: 10px;
            border: none;
            outline: none;
            resize: none;
            font-family: $main-font;
        }
        input[type="submit"] {
            width: 100px;
            border: none;
            cursor: pointer;
            padding: 10px; 
        }
    }
}
.contact__form {
    text-align: center;
    width: 40%;
    padding: 40px;
    background: $white;
    border-radius: 10px;
    backdrop-filter: blur(5px);
    background: rgba(255, 255, 255, 0.1);
    margin-bottom: 30px;
}