/*  Services  */

.services {
    &__inner {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    &__card {
        border: 1px solid #ccc;
        border-radius: 10px;
        padding: 20px;
        text-align: center;
        margin: 15px;
        &:hover {
            background-color: $purple-light;
            .services__card-title,
            .services__card-text {
                color: $white;
            }
        }
        &--01 {
            border: 1px solid $green;
            &:hover {
                background-color: $green;
            }
        }
        &--02 {
            border: 1px solid $blue;
            &:hover {
                background-color: $blue;
            } 
        }
        &--03 {
            border: 1px solid $yellow;
            &:hover {
                background-color: $yellow;
            } 
        }
        &-title {
            color: $white;
            margin-bottom: 20px;
            &--01 {
                color: $green;
            }
            &--02 {
                color: $blue; 
            }
            &--03 {
                color: $yellow;
            }
        }
        &-text {
            line-height: 2;
        }
        img {
            max-width: 150px;
        }
        a {
            color: $white; 
        }
    }
    &__button {
        margin-top: 50px;
    }
}

//Services_2.html
.services__card.hover {
    background-repeat: no-repeat;
    background-size: cover;
    border: none;
    width: 300px;
    height: 300px;
    .services__card-content.hover {
        display: none;
    }
    &:hover {
        background: $purple-light; 
        .services__card-content.hover {
            display: block;
        }
    }
}
