/*  Circle  */
.wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}
.circle {
    transform-style: preserve-3d;
    animation: animate 8s linear infinite;
    font-family: monospace;
    .char {
        position: absolute;
        top: -300px;
        left: 0;
        color: $purple-dark;
        background-color: $white;
        font-size: 4em;
        padding: 5px 12px;
        border-top: 4px solid $purple-light;
        border-bottom: 4px solid $purple-light;
        transform-style: preserve-3d;
        transform-origin: center;
        transform: rotateY(calc(var(--char-index) * 12deg)) translateZ(250px);
    }
    &-section {
        padding: 100px; 
    }
    &-content {
        border-radius: 30px;
        padding: 100px;
        backdrop-filter: blur(5px);
        background: rgba(255, 255, 255, 0.1);
        text-align: center;
        z-index: 10;
    }
    &-title {
        color: $white;
        font-size: 90px;
    }
    &-card {
        background-color: $purple-light;
        border: none;
        &:hover {
            background-color: $purple-dark; 
        }
    }
}