/*  Settings  */
body {
  background-color: #351E8A;
  color: #fff;
  font-family: "Poppins", sans-serif; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px; }

.wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#f00, #f0f);
  clip-path: circle(25% at right 60%); }

.wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#2196f3, #e91e63);
  clip-path: circle(20% at 10% 10%);
  z-index: -10; }

.section {
  padding: 50px 0;
  text-align: center; }

a {
  text-decoration: none;
  color: #000; }

ul {
  list-style: none; }

.title {
  text-align: center;
  font-size: 48px;
  font-weight: 700;
  color: #fff;
  font-family: "Poppins", sans-serif; }

.active {
  text-align: center;
  text-transform: uppercase;
  color: #18DF9E;
  margin-bottom: -20px; }

.button {
  display: inline-block;
  border-radius: 10px;
  background: linear-gradient(#FF7133, #F79B15);
  border: none;
  padding: 20px 40px;
  color: #fff;
  font-weight: 700;
  font-size: 18px; }

.button:hover {
  background: #F79B15; }

.form-input {
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #ccc;
  max-width: 300px; }

#typed {
  display: inline; }

.back-to-top {
  background: #18DF9E;
  border-radius: 50%;
  position: fixed;
  bottom: -50px;
  padding: 10px 12px;
  right: 10px;
  cursor: pointer;
  transition: 0.3s all;
  z-index: 10; }

.back-to-top.active {
  bottom: 50px;
  transition: 0.3s all; }

.top-btn {
  width: 24px;
  height: 24px; }

.error__box {
  min-height: 80vh;
  text-align: center;
  border-radius: 30px;
  padding: 30px;
  backdrop-filter: blur(5px);
  background: rgba(255, 255, 255, 0.1); }
  .error__box h1 {
    font-size: 80px; }
  .error__box h2 {
    font-size: 70px;
    margin-bottom: 50px; }

.counting-sec {
  padding: 40px 0;
  width: 100%; }

.inner-width {
  display: flex; }

.col {
  flex: 1;
  text-align: center;
  padding: 20px;
  color: #fff;
  text-transform: uppercase; }

.num {
  font-size: 40px;
  margin: 20px 0; }

.control ul {
  display: flex;
  justify-content: space-around; }

/*  Animation */
@keyframes image {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(10px); }
  100% {
    transform: translateY(0px); } }

@keyframes animate {
  0% {
    transform: perspective(1000px) rotateY(360deg) rotateX(15deg); }
  100% {
    transform: perspective(1000px) rotateY(10deg) rotateX(15deg); } }

/*  Header  */
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px; }

