/*  Slider  */
.team-slider {
  padding: 30px 0 120px; }
  .team-slider__text {
    margin-bottom: 50px; }

.slider {
  margin: 0 -20px; }
  .slider__item {
    padding: 0 20px; }
    .slider__item-img {
      width: 350px;
      height: 400px;
      border-radius: 10px;
      object-fit: cover;
      margin-bottom: 25px; }
    .slider__item-info {
      margin-bottom: 10px;
      line-height: 28px; }
    .slider__item-author {
      text-decoration: none;
      font-weight: 700;
      color: #544837; }
    .slider__item-title {
      text-decoration: none;
      font-size: 24px;
      line-height: 34px;
      font-weight: 700;
      transition: all .3s;
      margin-bottom: 80px;
      display: inline-block; }
      .slider__item-title:hover {
        color: #ff9900; }

.slick-slide {
  outline: #fff; }

.slick-dots {
  list-style: none;
  display: flex;
  justify-content: center; }
  .slick-dots button {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: none;
    font-size: 0;
    background-color: #EFEFEF;
    padding: 0;
    margin: 0 5px; }

.slick-active button {
  background-color: #F79B15; }

.testimonial {
  background-color: #4e34af;
  border-radius: 10px;
  margin-right: 20px;
  padding: 30px;
  margin: 20px; }
  .testimonial-top {
    display: flex;
    align-items: center;
    margin-bottom: 30px; }
  .testimonial-bottom {
    line-height: 1.8; }
  .testimonial img {
    margin-right: 20px;
    width: 80px;
    height: 80px;
    border-radius: 50%; }

/*  Footer  */
.footer {
  margin-top: 100px;
  background-color: #4e34af;
  color: #fff; }
  .footer__inner {
    display: flex;
    justify-content: space-between;
    align-items: top;
    flex-wrap: wrap; }
  .footer__column {
    margin: 0 30px 0 0; }
  .footer__contacts {
    display: flex;
    align-items: center; }
    .footer__contacts img {
      margin-right: 10px; }
  .footer__logo {
    margin: 20px 0; }
  .footer__text {
    max-width: 300px;
    margin: 0 auto;
    line-height: 1.5;
    margin-bottom: 30px; }
  .footer__social img {
    margin-right: 30px; }
  .footer__menu {
    padding: 0; }
    .footer__menu-link {
      color: #fff;
      line-height: 2; }
      .footer__menu-link:hover {
        color: #F79B15; }
  .footer__form-input {
    margin-bottom: 15px;
    max-width: 80%; }

/*  Media  */
@media (max-width: 1135px) {
  .first-page__title {
    font-size: 60px; }
    .first-page__title span {
      font-size: 60px; } }

@media (max-width: 995px) {
  .first-page__title {
    font-size: 50px; }
    .first-page__title span {
      font-size: 50px; }
  .portfolio__inner {
    flex-wrap: wrap;
    width: 50vw;
    margin: 0 auto; }
  .pricing__inner {
    flex-wrap: wrap; }
  .pricing__item {
    width: 60vw;
    margin: 0 auto;
    margin-bottom: 50px; }
  .contact {
    padding: 50px; }
    .contact-container {
      flex-direction: column; }
    .contact__info {
      margin-bottom: 40px;
      width: 60vw; }
    .contact__form {
      width: 60vw; }
    .contact__inputs {
      display: block; }
  .services__inner {
    flex-wrap: wrap; }
  .services__card {
    width: 60vw;
    margin: 0 auto;
    margin-bottom: 50px; } }

@media (max-width: 860px) {
  .first-page__title {
    font-size: 47px; }
    .first-page__title span {
      font-size: 47px; }
  .work__image, .work__images, .work__content {
    width: 80vw; }
  .work__images--1, .work__images--2 {
    width: 38vw; } }

@media (max-width: 769px) {
  .menu__list {
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background-color: #4e34af;
    text-align: center;
    transition: all .3s;
    transform: translateY(-300%);
    min-height: 100vh;
    z-index: 20;
    margin: 0;
    padding: 0;
    padding-top: 70px; }
    .menu__list--active {
      transform: translateY(0%);
      transition: all .3s; }
    .menu__list-link {
      display: block;
      padding: 30px 0;
      color: #fff; }
  .menu-btn {
    display: block;
    width: 50px;
    height: 50px;
    position: relative;
    z-index: 10; }
    .menu-btn span,
    .menu-btn span::before,
    .menu-btn span::after {
      position: absolute;
      width: 40px;
      height: 2px;
      top: 50%;
      margin-top: -1px;
      left: 50%;
      margin-left: -20px;
      background-color: #fff; }
    .menu-btn span::before,
    .menu-btn span::after {
      content: '';
      display: block;
      transition: 0.2s; }
    .menu-btn span::before {
      transform: translateY(-10px); }
    .menu-btn span::after {
      transform: translateY(10px); }
    .menu-btn__active {
      z-index: 21; }
      .menu-btn__active span {
        height: 0; }
        .menu-btn__active span::before {
          transform: rotate(45deg); }
        .menu-btn__active span::after {
          transform: rotate(-45deg); }
  .menu__list-item + .menu__list-item {
    margin-left: 0; }
  .first-page__inner {
    flex-direction: column-reverse;
    text-align: center; }
  .first-page__content, .first-page__img {
    width: 100%;
    margin-left: 0; }
  .first-page__title {
    font-size: 72px; }
    .first-page__title span {
      font-size: 72px; }
  .first-page__content-center h1 {
    font-size: 72px; }
    .first-page__content-center h1 span {
      font-size: 72px; }
  .about__image {
    width: 100%; }
  .about__content {
    width: 100%;
    margin-left: 0; }
  .about__inner {
    flex-direction: column-reverse; }
  .team__inner {
    padding: 100px 0; }
  .team__item {
    display: none; }
  .price__inner {
    flex-wrap: wrap;
    width: 60vw;
    margin: 0 auto; }
  .contact__info, .contact__form {
    width: 80vw; } }

@media (max-width: 650px) {
  .portfolio__inner {
    width: 90vw; }
  .price__inner {
    width: 90vw; }
  .work__inner {
    padding: 10px; }
  .work__images {
    display: block; }
    .work__images--1, .work__images--2 {
      width: 80vw;
      height: 50vh;
      margin-bottom: 30px; }
  .newsletter__image {
    display: none; } }

@media (max-width: 478px) {
  .circle {
    display: none; } }

@media (max-width: 375px) {
  .first-page {
    padding: 0; }
    .first-page__inner {
      padding: 10px; }
    .first-page__title {
      font-size: 30px; }
      .first-page__title span {
        font-size: 30px; }
    .first-page__content-center h1 {
      font-size: 60px; }
      .first-page__content-center h1 span {
        font-size: 60px; }
  .title {
    font-size: 30px; }
  .circle-section, .circle-content {
    padding: 0; }
  .services__inner {
    width: 90vw; }
  .price::before, .price::after {
    display: none; }
  .newsletter__inner {
    padding: 50px 10px; }
  .newsletter__left {
    width: 280px; }
  .newsletter__form-input {
    width: 280px; }
  .contact {
    padding: 10px; }
    .contact__img {
      display: none; }
    .contact-container {
      width: 80%; }
    .contact__form {
      width: 90%;
      text-align: center; }
    .contact__input {
      width: 90%; }
  .footer__column {
    margin-right: 0;
    width: 100%; }
  .footer__social {
    display: flex;
    justify-content: space-between; }
    .footer__social img {
      margin-right: 0; } }
