/*  About  */
.about {
    &__inner {
        display: flex;
        justify-content: space-around;
        align-items: center; 
    }
    &__image,
    &__content {
        width: 50%;
    }
    &__content {
        margin-left: 50px;
    }
    // &-box {
    //     border-radius: 30px;
    //     padding: 30px;
    //     backdrop-filter: blur(5px);
    //     background: rgba(255, 255, 255, 0.1);
    // }
    &-img {
        max-width: 100%;
        animation: image 2s linear infinite;
    }
    &-text {
        text-align: justify;
        max-width: 70vw;
        margin: 0 auto;
        margin-bottom: 30px;
        line-height: 2;
    }
}