
.first__demo {
    min-height: 100vh;
    text-align: center;
    &-h1 {
        text-transform: uppercase;
        font-size: 80px;
    }
    &-h2 {
        font-size: 80px;
        margin-bottom: 200px;
    }
}

.demo {
    padding-top: 200px;
    background-color: $purple-light;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center; 
    }
    &__box {
        margin-bottom: 30px; 
    }
    &__card {
        border: 1px solid #fff;
        background-repeat: no-repeat;
        background-size: cover;
        width: 280px;
        height: 280px;
        border-radius: 10px;
        margin-bottom: 30px;
        background-color: #ccc;
        overflow: hidden;
        img {
            width: 100%;
        }
        &-contact,
        &-error {
            height: 110%;
        }
    }
}
video {
    width: 90vw;
}

