.demo-link {
    display: inline-block;
}

.header-demo {
    display: flex;
    justify-content: center;
    padding-top: 30px;
    margin-bottom: 50px;
}

.menu-demo {
    display: flex;
    li {
        position: relative; 
    }
    li:hover > ul {
        display: block;
    }
    a {
        margin: 0 10px;
        color: $white;
        text-decoration: none;
        display: block;
        text-align: center;
        height: 40px;
        line-height: 40px;
        transition: all .5s;
        &:hover {
            color: $yellow;  
        }
    }
    ul {
        z-index: 20;
        border-radius: 10px;
        background: #fff;
        position: absolute;
        left: 0;
        top: 100%;
        display: none;
        padding: 0 10px;
        a {
            color: $purple-dark;
        }
        ul {
            display: none;
            top: 0;
            left: 100%;
        }
        li {
            width: 120px;
        }
    }
}



.features {
    &__inner {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
   &__item {
       background-color: #fff;
       color: $background-color;
       border-radius: 10px;
       padding: 20px;
       width: 200px;
       height: 200px;
       margin: 20px 10px;
       text-align: center;
       img {
           max-width: 100px;
       }
   }
}
