/*  Team Quotes  */
.team {
    background-color: $purple-dark;
    &__inner  {
        padding: 230px 0 380px;
        position: relative;
    }
    &__item {
        position: absolute;
        width: 98px;
        height: 98px;
        &-img {
            width: 98px;
            height: 98px;
            border-radius: 50%;
            overflow: hidden;
            object-fit: cover;
        }
        &-text {
            background-color: $purple-light;
            padding: 15px 20px;
            color: #fff;
            border-radius: 26px;
            box-shadow: 0px 2px 8px rgba(86, 117, 146, 0.1);
            position: absolute;
            top: -30px;
            right: 75px;
            white-space: nowrap;
            &--left {
                top: -40px;
                right: auto;
                left: 60px;
            }
        }
        &--1 {
            right: 95px;
            top: 115px; 
        }
        &--2 {
            left: 0;
            top: 223px; 
        }
        &--3 {
            bottom: 250px;
            right: 0;
        }
        &--4 {
            left: 210px;
            bottom: 220px;
        }
        &--5 {
            right: 300px;
            bottom: 65px;
        }
        &-text--right::after,
        &-text--right::before,
        &-text--left::after,
        &-text--left::before {
            content:"";
            position: absolute;
            background-color: $purple-light;
            border-radius: 50%;
        }
        &-text--right::after,
        &-text--left::after {
            width: 17px;
            height: 17px;
            bottom: -7px;
            right: 30px;
        }
        &-text--right::before,
        &-text--left::before {
            width: 9px;
            height: 9px;
            bottom: -20px;
            right: 9px;
        }
        &-text--left::after {
            left: 30px;
            right: auto;
        }
        &-text--left::before {
            left: 9px;
            right: auto;
        }
    }
    &__box {
        text-align: center;
        max-width: 550px;
        margin: 0 auto;
    }
    &__title {
        padding-top: 50px;
        margin-bottom: 20px;
        font-size: 36px;
        line-height: 54px;
        font-weight: 700;
        color: #fff;
    }
}