/*  Testimonials Swiper  */
.testimonial-slider {
    padding-top: 100px;
}
.swiper {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    &-container {
        width: 100%;
        padding-top: 50px;
        padding-bottom: 50px; 
    }
    &-slide {
        background-position: center;
        background-size: cover;
        width: 300px;
        box-shadow: 0 15px 50px rgba(0,0,0,0.2);
        filter: blur(4px); 
        border-radius: 10px;
        background-color: $purple-dark;
        &-active {
            filter: blur(0px);
            background: $purple-light; 
        }
    }
    &-box {
        position: relative;
        width: 80%;
        padding: 20px;
        padding-top: 90px;
        color: $white;
        line-height: 1.8;
    }
    &-quote {
        position: absolute;
        top: 20px;
        right: 30px;
        width: 50px; 
    }
    &-content {
        width: 100%;
        margin: 0 auto;
    }
    &-details {
        display: flex;
        align-items: center;
        margin-top: 20px;
    }
    &__image-box {
        position: relative;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 15px;
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;  
        }
    }
    h3 {
        font-size: 16px;
        font-weight: 400;
        letter-spacing: 1px;
        color: $yellow;
        line-height: 1.1em;
    }
    span {
        font-size: 12px;
        color: $white;
    }
}
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right {
    background-image: none;
}