/*  Price  */
.price { 
    padding-top: 100px;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(#f00, #f0f);
        clip-path: circle(20% at right 60%);
    }
    &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(#2196f3, #e91e63);
        clip-path: circle(20% at left 30%);
        z-index: -10;
    }
    &__inner {
        display: flex;
        justify-content: space-between;
    }
    &__item {
        text-align: center;
        backdrop-filter: blur(5px);
        background: rgba(173, 173, 173, 0.1);
        padding: 0 50px 60px;
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        margin: 20px;
    }
    &__subtitle {
        width: 100%;
        height: 80px;
        color: #fff;
        font-size: 40px;
        font-weight: 700;
        padding-top: 30px;
        background-color: #18DF9E;
        border-bottom-left-radius: 100px;
        border-bottom-right-radius: 100px;
        &--01 {
            background-color: $green; 
        }
        &--02 {
            background-color: $blue;
        }
        &--03 {
            background-color: $yellow;
        }
    }
    &__item {
        &-title {
            padding-top: 50px;
            font-weight: 700;
            font-size: 32px;
            line-height: 40px;
            margin-bottom: 25px;
            &--01 {
                color: $green;
            }
            &--02 {
                color: $blue;
            }
            &--03 {
                color: $yellow;
            }
        }
        &-text {
            line-height: 1.8;
            margin-bottom: 60px;
            text-align: center;
        }
    }
    &__list {
        margin-bottom: 75px;
        padding: 0;
        &-item {
            margin-bottom: 12px;
        }
    }
    &__btn {
        color: $white;
        font-size: 17px;
        background: linear-gradient($orange, $yellow);
        font-weight: 700;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        text-align: center;
        padding: 20px 10px;
        &--01 {
            background: $green; 
        }
        &--02 {
            background: $blue; 
        }
        &--03 {
            background: $yellow; 
        }
    }
}


.pricing {
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 50px;
    }
    &__item {
        position: relative;
        border-radius: 10px;
        padding: 15px;
        text-align: center;
        width: 300px;
        height: 400px;
        margin: 10px;
        transform-style: preserve-3d;
        background: rgba(173, 173, 173, 0.1); 
        &-img {
            max-width: 200px;
            position: absolute;
            top: 30px;
            left: 50%;
            transition: 0.5s;
            transform-style: preserve-3d;
            transform: translate3d(-50%,-50%,100px) rotate(-15deg);
            margin-bottom: 30px;
        }
        &-title {
            font-size: 30px;
            margin: 30px 0;
            position: absolute;
            top: 100px;
            left: 0;
            text-align: center;
            color: $white;
            width: 100%;
            transform-style: preserve-3d;
            transform: translate3d(0,0,75px);
            transition: 0.5s;
            z-index: 10;
        }
        &-price {
            position: absolute;
            font-size: 35px;
            font-weight: 700;
            top: 200px;
            left: 50%;
            transform-style: preserve-3d;
            transform: translate3d(-50%,0,75px);
        }
        &-btn {
            display: inline-block;
            padding: 15px 30px;
            border-radius: 100px;
            background: linear-gradient($orange, $yellow);
            color: #fff;
            margin-bottom: 30px;
            position: absolute;
            bottom: 0;
            left: 50%;
            text-align: center;
            transform-style: preserve-3d;
            transform: translate3d(-50%,0,75px);
            transition: 0.5s;
            z-index: 10;
        }
    }
}