/*  First Page  */
.first-page {
    padding-top: 30px;
    z-index: 10;
    position: relative;
    margin-bottom: 100px;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-radius: 30px;
        padding: 30px;
        backdrop-filter: blur(5px);
        background: rgba(255, 255, 255, 0.1);
    }
    &__content {
        width: 50vw;
        &-center {
            width: 100%;
            text-align: center;
            h1 {
                font-size: 100px;
                span {
                    font-size: 100px; 
                }
            }
        }
    }
    &__img {
        width: 50vw;
        margin-left: 10px;
        animation: image 3s linear infinite;
    }
    &__title {
        font-size: 60px;
        line-height: 1.5;
        font-weight: 700;
        color: $white;
        margin-bottom: 50px;
        span {
            font-size: 60px;
            font-weight: 700;
            color: #18DF9E;
        }
        img {
            max-width: 30px;
        }
    }
}